export default {
	data() {
		return {
			seear: ['全部', '待付款', '待收货', '待评价'], //导航栏文字数据
			curr: 0, //导航栏初始下标
			allorder: [], //全部订单
			unpaid: [], //待支付
			delivery: [], //待收货
			evaluate: [], //待评价
			hig: '', //屏幕的高度
			order: [], //全部订单渲染数据
			paid: [], //待支付渲染数据
			livery: [], //待收货渲染数据
			luate: [], //待评价渲染数据
			num: 10 //每次加载数据
		}
	},
	methods: {
		setCurr(e) { //切换轮播页面事件
			let thisCurr = e.detail.current || e.currentTarget.dataset.id || 0;
			this.curr = thisCurr;
		},
		back() { //返回
			uni.navigateBack({
				delta: 1
			})
		},
		btns() { //点击加载更多
			this.num += 10;
			this.order = this.allorder.slice(0, this.num)
			this.paid = this.unpaid.slice(0, this.num)
			this.livery = this.delivery.slice(0, this.num)
			this.luate = this.evaluate.slice(0, this.num)
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(function() {
				uni.hideLoading();
			}, 500);
		}

	},
	onLoad() {
		let _that = this
		uni.getSystemInfo({ //获取屏幕高度
			success: function(res) {
				_that.hig = "height: " + (res.windowHeight - 100) + "px";
			}
		});
		let _this = this;

		let str = uni.getStorage({
			key: 'use'
		})
		//获取全部订单
		str.then(function(result) {
			uni.request({
				url: "http://ceshi3.dishait.cn/api/order/all",
				header: {
					'token': result[1].data
				},
				method: "POST",
				success(data) {
					_this.allorder = data.data.data
					_this.order = _this.allorder.slice(0, 10)
					console.log(_this.order)
				},
				fail(e) {
					console.log('报错')
				}
			})
		})
		//获取待付款
		str.then(function(result) {
			uni.request({
				url: "http://ceshi3.dishait.cn/api/order/paying",

				header: {
					'token': result[1].data
				},
				method: "POST",
				success(data) {
					_this.unpaid = data.data.data
				},
				fail(e) {
					console.log('报错')
				}
			})
		})
		//待收货
		str.then(function(result) {
			uni.request({
				url: "http://ceshi3.dishait.cn/api/order/receiving",

				header: {
					'token': result[1].data
				},
				method: "POST",
				success(data) {
					_this.delivery = data.data.data
				},
				fail(e) {
					console.log('报错')
				}
			})
		})
		// 待评价
		str.then(function(result) {
			uni.request({
				url: "http://ceshi3.dishait.cn/api/order/reviewing",

				header: {
					'token': result[1].data
				},
				method: "POST",
				success(data) {
					_this.evaluate = data.data.data
				},
				fail(e) {
					console.log('报错')
				}
			})
		})

	},
	onShow() {
		uni.showLoading({
			title: '正在加载'
		});
		setTimeout(function() {
			uni.hideLoading();
		}, 1000);
	}
}
